import React, { useEffect, useState } from "react";
import axios from "axios";
import Notification from "../../../component/AlertMessageBox";
import ConfirmDialog from "../../../component/ConfirmDialog";
import ImgMediaCard from "../../../component/Card";
import { makeStyles } from "@material-ui/core/styles";
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import ListSubheader from "@material-ui/core/ListSubheader";

import Modal from "@material-ui/core/Modal";
function rand() {
  return Math.round(Math.random() * 20) - 10;
}

function getModalStyle() {
  const top = 50+ rand();
  const left = 50+ rand();

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
    
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-around",
    overflow: "hidden",
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: 500,
    height: 450,
  },
  icon: {
    color: "rgba(255, 255, 255, 0.54)",
  },
  paper: {
    position: "absolute",
    width: 600,
    height:400,
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
  profileimage: {
    width: 150,
    height: 150,
    margin:8,
  
  },
}));
const Typography = () => {
  const classes = useStyles();
  const [modalStyle] = useState(getModalStyle);
  const [open, setOpen] = useState(false);
  const [driverL, setdriverL] = useState(
""  );
  const [VichaialL, setVichaialL] = useState("");
  const [frontOfV, setfrontOfV] = useState("");
  const [backOfV, setbackOfV] = useState("");
  const [leftSideOfV, setleftSideOfV] = useState("");
  const [RightSideOfV, setRightSideOfV] = useState("");

  const handleOpen = (
    leftSideOfvehicle,
    rightSideOfvehicle,
    vehicleLicense,
    drivingLicense,
    backOfvehicle,
    frontOfvehicle
  ) => {
    setdriverL(drivingLicense);
    setVichaialL(vehicleLicense);
    setRightSideOfV(rightSideOfvehicle);
    setleftSideOfV(leftSideOfvehicle);
    setbackOfV(backOfvehicle);
    setfrontOfV(frontOfvehicle);
    setOpen(true);
  };

  // {console.log(frontOfV) + "/////" + console.log(photo)}

  const handleClose = () => {
    setOpen(false);
  };

  const body = (
    <div style={modalStyle} className={classes.paper}>
      <h2 id="simple-modal-title">الملفات الخاصة بالكابتن</h2>
       <img
        className={classes.profileimage}
        src={`https://jetek-tr5el.ondigitalocean.app${driverL}`}
      />
    
     
       <img
        className={classes.profileimage}
        src={`https://jetek-tr5el.ondigitalocean.app${VichaialL}`}
      />
       <img
        className={classes.profileimage}
        src={`https://jetek-tr5el.ondigitalocean.app${frontOfV}`}
      />
       <img
        className={classes.profileimage}
        src={`https://jetek-tr5el.ondigitalocean.app${backOfV}`}
      />
      <img
        className={classes.profileimage}
        src={`https://jetek-tr5el.ondigitalocean.app${leftSideOfV}`}
      />
       <img
        className={classes.profileimage}
        src={`https://jetek-tr5el.ondigitalocean.app${RightSideOfV}`}
      />
      
    </div>
  );
  const [CaptinData, setCaptinData] = useState([]);
  const [notify, setNotify] = useState({
    isOpen: false,
    message: "",
    type: "",
  });
  const [confirmDialog, setConfirmDialog] = useState({
    isOpen: false,
    title: "",
    subTitle: "",
  });

  const apiurl = "https://jetek-tr5el.ondigitalocean.app/admin";
  const authRequest = axios.create({
    baseURL: apiurl,
    headers: {
      "auth-token":
        "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6IjYwNWZhNDliMjUzNjI1MjliMDMzMTBhMyIsImlhdCI6MTYxNzI3OTI5NSwiZXhwIjoxNjE3MzY1Njk1fQ.vUJVRdhU3sMKhnCrZIE9W-7fIAumUNQJ-N8vOIpP3XY",
    },
  });

  const fetch = async () => {
    try {
      const captins = await authRequest.get(`/getAllCaptins`);
      // handle success
      // console.log(users.data);
      await setCaptinData(captins.data);
      // console.log("state seting");
     
    } catch (error) {
      console.log(error);
    }
  };

  const confirm = (id) => {
    setConfirmDialog({
      isOpen: true,
      title: "   هل انت متاكد من حذف الكابتن ؟",
      subTitle: "لا يمكنك التراجع عن هذة العملية ",
      onConfirm: () => {
        deleteUser(id);
      },
    });
  };

  const deleteUser = async (id) => {
    setConfirmDialog({
      ...confirmDialog,
      isOpen: false,
    });
    // console.log(id);
    var body = {
      id: id,
    };
    axios
      .post("https://jetek-tr5el.ondigitalocean.app/admin/deleteCaptin", body)
      .then((response) => {
        console.log(response);
        if (response.status == 200) {
          fetch();
          setNotify({
            isOpen: true,
            message: "تم حذف الكابتن",
            type: "error",
          });
        }
      })
      .catch((err) => console.log(err));
  };
  const changeStateFunction = async (id) => {
    console.log(id);
    setConfirmDialog({
      ...confirmDialog,
      isOpen: false,
    });
    // console.log(id);
    var body = {
      id: id,
    };
    // const users = await authRequest.post(`/deleteUser`)

    axios
      .post("https://jetek-tr5el.ondigitalocean.app/admin/disableCaptin", body)
      .then((response) => {
        console.log(response);
        if (response.status == 200) {
          fetch();

          setNotify({
            isOpen: true,
            message: "تم تعديل  المستخدم",
            type: "success",
          });
        }
      })
      .catch((err) => console.log(err));
  };
  useEffect(() => {
    fetch();
  }, []);
  return (
    <>
      <div className={classes.root}>
        <GridList cellHeight={180} className={classes.gridList}>
          <GridListTile key="Subheader" cols={2} style={{ height: "auto" }}>
            <ListSubheader component="div">الكباتن</ListSubheader>
          </GridListTile>
          {CaptinData.map((captin) => (
            <ImgMediaCard
              data={captin}
              statusChangeHandler={changeStateFunction}
              statusRemoveHandler={confirm}
              viewFilesHandler={handleOpen}
            />
          ))}
           {console.log(driverL)+"dl"}
      {console.log(VichaialL)+"vl"}
      {console.log(frontOfV)+"fv"}
      {console.log(backOfV)+"bv"}
      {console.log(leftSideOfV)+"lv"}
      {console.log(RightSideOfV)+"rv"}
        </GridList>
      </div>
      <Notification notify={notify} setNotify={setNotify} />
      <ConfirmDialog
        confirmDialog={confirmDialog}
        setConfirmDialog={setConfirmDialog}
      />
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {body}
      </Modal>
    </>
  );
};

export default Typography;
